.App {
  text-align: center;
  display:flex;
flex-direction:column;
}

.Header {
  /*background-color: #282c34;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  /*justify-content: center;*/
  font-size: calc(10px + 2vmin);
  color: white;
  height: 270px;
}

.Title{
  font-size : 50px;
  font-weight: bold;
  padding: 30px;
}



.LinkContainer{
  background-color: #f5f5f5;
  background-color: #1A2125;
  padding: 15px;
  border-radius: 10px;
}
.LinkContainer:hover{
  background-color: #e1ede9;
  background-color: #244967;
  cursor: pointer;
}
.LinkContainer.Small{
  margin-top: 5px;
  font-size: 14px;
  padding: 5px;
}
.RoomLink{
  color:white;
}

.Column{
  float: left;
  width: 33.33%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 280px;
  white-space: nowrap;
}
.Column.Chatlog{
  position: relative;
  padding-right: 20px;
  padding-left: 20px;
  /*background-color: #999999;*/
}
.Column.Stats{
  position: relative;
  padding-right: 20px;
  padding-left: 20px;
  /*background-color: #888888;*/
}
.Column.Replays{
  position: relative;
  padding-right: 20px;
  padding-left: 20px;
  /*background-color: #777777;*/
}
.ColumnTitle{
  font-size: 20px;
  font-weight: bold;
  color:white;
  margin-bottom: 20px;
}

.Row{
  /*height: 70vh;*/
  white-space: nowrap;
  overflow: hidden;
}

.Row:after{
  content:"";
  display: table;
  clear: both;
}

.Container{
  font-size: 15px;
  margin-top: 5px;
  margin-bottom: 5px;
  width: 260px;
  /*background-color: #F9FFFF;*/
  background-color: #f5f5f5;
  background-color: #1A2125;
  border-radius: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 5px;
  padding-left: 5px;
  color: white;
}
.Container:hover{
  background-color: #e1ede9;
  background-color: #244967;
}

.ContainerChat{
  position: absolute;
  display: none;
  flex-direction: column;
  align-items: center;
  white-space: nowrap;
  font-size: 15px;
  margin-top: -18px;
  margin-left: -10px;
  width: 260px;
  background-color: #244967;
  border-radius: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 5px;
  padding-left: 5px;
  color: white;
}
.ContainerChat:hover{
  display: flex;
}
.ChatReplay{
  margin-bottom: 3px;
  display: inline-block;
  background-color: #244967;
  border-radius: 5px;
  padding-left: 10px;
  padding-right: 10px;
  color: white;
}
.ChatReplay:hover{
  background-color: #356e9c;

}
.ChatReplay:hover .ContainerChat{
  display: flex;
}

.Container.Click:hover{
  cursor: pointer;
}
.InfoTitle{
  font-weight: bold;
}
.BigDigit{
  font-weight: bold;
  font-size: 30px;
}
.InfoBox{
  display: inline-block;
  margin-left: 10px;
  margin-right: 10px;
}
.InfoBoxWide{
  display: inline-block;
  text-align: center;
  margin-left: 10px;
  margin-right: 10px;
  min-width: 60px;
}
.InfoPlainTitle{
  font-size: 12px;
  margin-top: 5px;
}
.SubTitle{
  font-size: 8px;
  white-space: pre-wrap;
}
.SubTitleOffset{
  font-size: 10px;
  font-weight: 600;
}
.SmallDigit{
  font-size: 20px;
  font-weight: bold;
}
.SmallDigitSmall{
  font-weight: bold;
  font-size: 12px;
}
.TextLeft{
  position: absolute;
  margin-top: -1px;
  margin-left: 10px;
  font-size: 12px;
  font-weight: bold;
}
.TextRight{
  float: right;
  margin-right: 10px;
  margin-top: -1px;
  font-size: 12px;
  font-weight: bold;
}


.ChatContainer{
  min-height: 100vh;
  background-color: #1A2125;
  font-size: 14px;
  font-family: 'Open Sans', sans-serif;
  color: white;
  vertical-align: baseline;
  white-space: pre-wrap;
  line-height: 1.3;
  padding: 10px;
  padding-bottom: 100px;
  padding-top: 20px;
}
.ChatMessage{
  margin-bottom: 3px;
  display: inline-block;
}
.ChatMessageHost{
  color: #8ed2ab;
  margin-bottom: 3px;
  display: inline-block;
}
.ChatAnnouncement{
  border-left: 3px solid #c13535;
  padding-left: 5px;
  min-height: 12px;
  margin-bottom: 3px;
  padding-left: 5px;
  display: inline-block;
}
.ChatTimeStamp{
  display: inline-block;
  font-size: 10px;
  margin-right: 10px;
  color: #546c7a;
}

.CommandTitle{
  padding: 5px;
  padding-left: 10px;
  font-size: 18px;
  margin-top: 20px;
  margin-left: -20px;
  font-weight: bold;
  border-left: 3px solid #c13535;
}
.CommandContainer{
  margin-left: 20px;
}
.LeftTitle{
  margin-top: 20px;
  display: inline-block;
  font-weight: bold;
}
.CommandSyntax{
  display: inline-block;
  padding: 5px;
  margin-left: 10px;
  border-bottom: 3px solid #244967;
}
.CommandInfo{
  padding: 5px;
  margin-left: 10px;
  font-style: italic;
  white-space: pre-line;
  max-width: 1150px;
}
.Admin{
  width: 78px;
  padding: 2px;
  padding-left: 5px;
  padding-right: 5px;
  border-radius: 5px;
  margin-top: 20px;
  margin-bottom: -15px;
  background-color: #c13535;
}
